html {
  font-size: 100%;
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

li {
  list-style: none;
}

body {
  font-size: 1rem;
  font-family: var(--jakarta);
  background-color: var(--snow);
}
@media (min-width: 40em) {
  body {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
  }
}

.card {
  background-color: var(--white);
  outline: 2px solid red;
  padding-top: 1.5rem;
}
@media (min-width: 40em) {
  .card {
    border-radius: 0.9375rem;
    padding-top: 2.0625rem;
  }
}

.wrapper {
  max-width: 45.625rem;
  padding: 0 1rem;
  padding-top: 1.5rem;
  margin-inline: auto;
}
@media (min-width: 40em) {
  .wrapper {
    margin: 0 1.25rem;
    padding-top: 2.0625rem;
  }
}

h1, h2, h3 {
  margin-top: 0;
}

h1 {
  font-size: 1.25rem;
  font-size: clamp(1.25rem, 1.01rem + 1.02vw, 1.5rem);
  font-weight: 800;
}

body, button {
  font-size: 0.875rem;
  font-size: clamp(0.875rem, 0.76rem + 0.51vw, 1rem);
}

a, a:visited, a:active {
  text-decoration: none;
}

:root {
  --blue: #0A327B;
  --red: #F65552;
  --veryDarkBlue: #1C202B;
  --darkGrey: #5E6778;
  --grey: #939CAD;
  --lightGrey: #E5EFFA;
  --veryLightGrey: #DDE7EE;
  --snow: #F7FAFD;
  --white: #ffffff;
}

:root {
  --jakarta: "Plus Jakarta Sans", sans-serif;
}

.header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 2rem;
}
.header h1 {
  display: flex;
  align-items: flex-end;
  gap: 9px;
}
@media (min-width: 40em) {
  .header h1 {
    gap: 11px;
  }
}
.header span {
  font-size: 1rem;
  padding: 1px 11px 4px 11px;
  color: var(--white);
  border-radius: 6px;
  background-color: var(--blue);
}
.header__button {
  border: none;
  background-color: transparent;
  color: var(--darkGrey);
  cursor: pointer;
}
.header__button:hover {
  color: var(--blue);
}